<template>
    <div style="padding: 20px;">
        <h2>Template</h2>
        <div class="card p-3 shadow-sm mb-5">
        <script type="syntaxhighlighter" class="brush:html;gutter: false;">
            <![CDATA[
            <div>
                <p>
                    Name (received by params):
                    <input type="text" class="form-control" v-model="name">
                </p>
                <p>
                    Age:<input type="text" class="form-control" v-model="age">
                </p>
                <div>
                    Company:
                    <div class="input-group">
                        <input type="text" class="form-control" disabled="disabled"
                               v-model="company" placeholder="Please choose a company">
                        <span class="input-group-btn">
                            <button class="btn btn-default" type="button"
                                    @click="chooseCompany">Choose</button>
                        </span>
                    </div>
                </div>
                <p style="margin-top: 10px;">
                    <button type="button"
                        class="btn btn-default"
                        @click="ok">OK</button>
                </p>
            </div>
            ]]>
        </script>
        </div>
        <h2>Script</h2>
        <div class="card p-3 shadow-sm">
        <script type="syntaxhighlighter" class="brush:js;gutter: false;">
        <![CDATA[
            import cp from './Company';
            export default {
                props: {
                    name: String
                },
                data(){
                    return {
                        company: '',
                        age: 20
                    }
                },
                methods: {
                    ok(){
                        //close current modal dialog and return data
                        this.$emit('close', { item: this.item });
                    },
                    chooseCompany(){
                        this.$dlg.modal(cp, {
                            width: 400,
                            height: 300,
                            title: 'Company list',
                            callback: data=>{
                                this.company = data && data.name;
                            }
                        });
                    }
                }
            }
        ]]>
    </script>
        </div>
    </div>
</template>

<script>
export default {
  name: 'UserProfileCode',
  mounted () {
    window.SyntaxHighlighter.highlight()
  }
}
</script>
